import React, { useEffect, useRef, useState } from 'react';
import './App.css';
import solve, { clearGroup, gravity } from './solver/index';
import useWindowSize from './useWindowSize'
const imutils = require('imutils')

const colors = ['#8bd055', '#7dd6e1', '#e482b7', '#ae6dc9', '#2cbdde', '#333', '#0c3c7e', '#d7b48c', '#be2828', '#f2fc0c']
const initialGrid = `
  01310203121
  13230200221
  23231110203
  03310210001
  23101200021
  12111022133
  12012202233
  12033101223
  10301131033
  22233003213
  00210303233
  10302123323
`

let hasListener = false;
window.imutils = imutils;
const cv2 = window.cv;
const cv = window.cv;

let minx = 1000000;
let miny = 1000000;
let maxx = -1000000;
let maxy = -1000000;

function loadImgFromUrl(url) {
  return new Promise(resolve => {
    var img = document.createElement('img');
    img.src = url; // set src to blob url

    img.addEventListener('load', () => {
      const mat = window.cv.imread(img);
      resolve(mat);
    })
  })
}

function getDistance(x1, y1, x2, y2) {
  var a = Math.abs(x1 - x2);
  var b = Math.abs(y1 - y2);

  var c = Math.sqrt(a * a + b * b);

  return c;
}

function getClosest(items, x, y) {
  let minDistance = 1000000000;
  let result = null;

  items.forEach(({ center, char }) => {
    const currentDis = getDistance(center.x, center.y, x, y);
    if (currentDis < minDistance) {
      minDistance = currentDis;
      result = { center, char };
    }
  });
  return result
}

function cut_grid_out_image(inputimg) {
  let image = inputimg.clone();
  return new Promise(resolve => {
    loadImgFromUrl('img/grid_grayscale.png').then(gridImage => {
      let dst = new cv2.Mat();
      let mask = new cv2.Mat();
      cv.cvtColor(image, image, cv.COLOR_RGBA2GRAY, 0);
      cv2.matchTemplate(image, gridImage, dst, cv.TM_CCORR_NORMED, mask)
      let result = cv.minMaxLoc(dst, mask);

      image = inputimg.roi(new cv.Rect(result.maxLoc.x, result.maxLoc.y, gridImage.rows, gridImage.cols));
      resolve(image)
    })
  })
}

function find_templates(template, sensitivity, inputimg, char) {
  let image = inputimg.clone();
  // image = imutils.scaleToFit(image, preferredWidth)
  let dst = new cv2.Mat();
  let mask = new cv2.Mat();

  const centers = []

  let maxVal = 1;
  while (maxVal > sensitivity && centers.length < 33) {
    cv2.matchTemplate(image, template, dst, cv.TM_CCORR_NORMED, mask)
    let result = cv.minMaxLoc(dst, mask);
    maxVal = result.maxVal;
    if (maxVal < sensitivity) {
      break;
    }
    const { maxLoc } = result;
    const w = template.size().width;
    const h = template.size().height;
    let maxPoint = result.maxLoc;

    let point = new cv.Point(maxPoint.x + template.cols - 5, maxPoint.y + template.rows - 5);

    cv2.rectangle(image, { x: maxLoc.x + 5, y: maxLoc.y + 5 }, point, new cv.Scalar(255, 0, 0, 255), -1);
    centers.push({ center: { y: maxLoc.x + template.cols / 2, x: maxLoc.y + template.rows / 2 }, char })

    if (maxLoc.x < minx) minx = maxLoc.x;
    if (maxLoc.y < miny) miny = maxLoc.y;

    if (maxLoc.y + template.rows > maxy) {
      maxy = maxLoc.y + template.rows;
    }
    if (maxLoc.x + template.cols > maxx) {
      maxx = maxLoc.x + template.cols;
    }
  }

  image.delete();
  template.delete();

  return centers;
}

function App() {

  const gridRef = useRef()
  const [itemWidth, setItemWidth] = useState(0);
  const windowSize = useWindowSize();
  const [solved, setSolved] = useState()
  const [currentMove, setCurrentMove] = useState([])
  const [gridHistory, setGridHistory] = useState([])
  const [showAdvanced, setShowAdvanced] = useState(false);
  const [inputChar, setInputChar] = useState(0);
  const [isloading, setIsLoading] = useState(null)

  const [gridString, setGridString] = useState(initialGrid);

  const [options, setOptions] = useState({
    width: 11,
    height: 12,
    amountOfItems: 4,
    MIN_GROUP_SIZE: 2,
    MAX_NODES: 200,
    MIN_SEARCH_SECONDS: .1
  });

  const solvePuzzle = async () => {
    const res = await solve(gridString)
    setSolved(res);
    setCurrentMove(res.moves[0])
  }

  useEffect(() => {
    setItemWidth(gridRef.current.clientWidth / options.width)
  }, [gridRef, options, windowSize])

  useEffect(() => {
    if (hasListener) return;
    hasListener = true;
    document.querySelector('#imageupload').addEventListener('change', function () {
      if (this.files && this.files[0]) {
        setIsLoading(0)
        Promise.all([loadImgFromUrl('img/broek.png'), loadImgFromUrl('img/schoen.png'), loadImgFromUrl('img/shirt.png'), loadImgFromUrl('img/tas.png')]).then(([pants, shoe, shirt, bag]) => {
          var img = document.createElement('img');
          img.addEventListener('load', () => {
            let mat = window.cv.imread(img);
            URL.revokeObjectURL(img.src);
            // cut_grid_out_image(mat).then(mat => {
            //   console.log(mat);
            // });

            const sensitivity = 0.95;
            const shirtssres = find_templates(shirt, sensitivity, mat, '0')
            setIsLoading(25)
            const shoesres = find_templates(shoe, sensitivity, mat, '3')
            setIsLoading(50)
            const bagsres = find_templates(bag, sensitivity, mat, '1');
            setIsLoading(75)
            const pantsres = find_templates(pants, sensitivity, mat, '2')

            const templates = [...pantsres, ...shoesres, ...shirtssres, ...bagsres];
            const grid = []
            const gridWidth = maxx - minx;
            for (let x = 0; x < 12; x++) {
              grid[x] = []
              for (let y = 0; y < 11; y++) {
                const itemWidth = gridWidth / 11;
                const closest = getClosest(templates, miny + (x + .5) * itemWidth, minx + (y + .5) * itemWidth);
                grid[x][y] = closest.char
              }
            }

            mat.delete();

            setGridString(stringifyGrid(grid))
            setIsLoading(100)
          })
          img.src = URL.createObjectURL(this.files[0]); // set src to blob url
        });

      }
    });
  }, [])

  const getChar = (x, y) => {
    const row = gridString.trim().split('\n')[x]

    if (row) {
      return row.trim().split('')[y]
    }

    return ''
  }

  const onCellClick = (x, y) => {
    const grid = gridString.trim().split('\n').map(row => row.trim().split(''))

    grid[x][y] = inputChar

    let newGridString = ''

    grid.forEach(row => {
      newGridString += row.join('') + '\n'
    })

    setGridString(newGridString);
  }

  const iscurrentMove = (x, y) => {
    return currentMove && currentMove.find(move => move.x === x && move.y === y);
  }

  const resetGrid = () => {
    let newGridString = ''

    Array.from(Array(options.height).keys()).forEach(x => {
      newGridString += Array.from(Array(options.width).keys()).map(k => '-').join('') + '\n'
    })

    setGridString(newGridString);
  }

  const reset = () => {
    if (solved) {
      setGridString(gridHistory[0]);
      setCurrentMove([])
      setSolved(null);
    }
    else {
      resetGrid();
      setCurrentMove([])
      setGridHistory([]);
    }
  }


  const parseGrid = (stringGrid) => {
    return stringGrid.trim()
      .split('\n')
      .map(str => str.trim().split(''));
  }

  const stringifyGrid = (grid) => {
    let newGridString = ''

    grid.forEach(row => {
      newGridString += row.join('') + '\n'
    })

    return newGridString;
  }

  const onNextMove = () => {
    const moveIndex = solved.moves.indexOf(currentMove)

    if (moveIndex >= solved.moves.length - 1) return;

    const newgridHistory = [...gridHistory];
    newgridHistory[moveIndex] = gridString;
    setGridHistory(newgridHistory);

    const newGrid = parseGrid(gridString);
    clearGroup(newGrid, currentMove);
    gravity(newGrid)

    setGridString(stringifyGrid(newGrid))
    setCurrentMove(solved.moves[moveIndex + 1])
  }

  const onPreviousMove = () => {
    const moveIndex = solved.moves.indexOf(currentMove)

    if (moveIndex === 0) return;

    setGridString(gridHistory[moveIndex - 1])
    setCurrentMove(solved.moves[moveIndex - 1])
  }

  const setProp = (name, value) => {
    setOptions({
      ...options,
      [name]: value
    })
  }

  return (
    <div className="App">
      <div className='options'>
        <div className='input-group'>
          <label>Amount of unique items</label>
          <input type='number' max={10} value={options.amountOfItems} onChange={(e) => setProp('amountOfItems', Number(e.target.value))} />

          <label>Minimum group size</label>
          <input type='number' value={options.MIN_GROUP_SIZE} onChange={(e) => setProp('MIN_GROUP_SIZE', Number(e.target.value))} />
        </div>

        <div className='input-group'>
          <label>Width</label>
          <input type='number' value={options.width} onChange={(e) => setProp('width', Number(e.target.value))} />

          <label>Height</label>
          <input type='number' value={options.height} onChange={(e) => setProp('height', Number(e.target.value))} />
        </div>

      </div>

      {
        showAdvanced && <div>
          <label>Max BFS nodes</label>
          <input type='number' max={10} value={options.MAX_NODES} onChange={(e) => setProp('MAX_NODES', Number(e.target.value))} />

          <label>Minimum search seconds</label>
          <input type='number' value={options.MIN_SEARCH_SECONDS} onChange={(e) => setProp('MIN_SEARCH_SECONDS', Number(e.target.value))} />
        </div>
      }

      <button onClick={() => setShowAdvanced(p => !p)}>{showAdvanced ? 'Hide' : 'Show'} advanced options</button>

      <div>
        <span style={{ display: 'block' }} >Tap to place: </span>
        {Array.from(Array(options.amountOfItems).keys()).map(k =>
          <span className='select-cell' onClick={() => setInputChar(k)}
            style={{
              width: itemWidth - 4,
              height: itemWidth - 4,
              background: colors[k],
              outline: inputChar === k ? '2px solid green' : '',
            }}
          ></span>
        )}
      </div>


      <span style={{ display: 'block' }} >Or upload an screenshot: <input id='imageupload' type='file' /></span>

      {isloading != null && <div>Loading {isloading}%</div>}

      <div className='grid' ref={gridRef}>
        {
          Array.from(Array(options.height).keys()).map(x =>
            <div className='row' key={x}>
              {
                Array.from(Array(options.width).keys()).map(y =>
                  <div
                    key={x + '_' + y + getChar(x, y)}
                    style={{ background: `${getChar(x, y) ? colors[getChar(x, y)] : ''}`, width: itemWidth - 4, height: itemWidth - 4 }}
                    className={'cell ' + (solved ? '' : 'clickable ') + (iscurrentMove(x, y) ? 'current-move' : '')}
                    onClick={() => !solved && onCellClick(x, y)}
                  >
                    {iscurrentMove(x, y) ? 'X' : ''}
                  </div>
                )
              }
            </div>
          )
        }

      </div>

      {
        !solved && <div>Total items: {Array.from(Array(options.amountOfItems).keys()).map(k => occurrences(gridString, k.toString())).join(', ')} (should all be {options.width * options.height / options.amountOfItems})</div>
      }

      {
        solved && <div>
          <button onClick={onPreviousMove}>Previous</button>
          <button onClick={onNextMove}>Next</button>
        </div>
      }


      <button className='secondary' onClick={reset}>{solved ? 'Reset' : 'Clear'}</button>

      {
        !solved && <button onClick={solvePuzzle}>Solve</button>
      }

    </div>
  );
}

export default App;


function occurrences(string, subString, allowOverlapping) {

  string += "";
  subString += "";
  if (subString.length <= 0) return (string.length + 1);

  var n = 0,
    pos = 0,
    step = allowOverlapping ? 1 : subString.length;

  while (true) {
    pos = string.indexOf(subString, pos);
    if (pos >= 0) {
      ++n;
      pos += step;
    } else break;
  }
  return n;
}